@charset "UTF-8";
@import "http://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css";
@import "http://yui.yahooapis.com/3.18.1/build/cssreset/cssreset-min.css";
body {
  user-select: none;
  cursor: default; }

.container {
  max-width: 960px;
  margin: 0 auto; }

.left {
  float: left; }

.right {
  float: right; }

.body-left,
.body-right {
  position: absolute;
  top: 62px;
  left: 0;
  bottom: 0;
  right: 0; }

.header {
  position: relative;
  width: 100%;
  height: 60px;
  border-bottom: 2px solid  #bbb;
  z-index: 999; }
  .header .level-selector {
    position: relative;
    height: 60px; }
    .header .level-selector .label {
      float: left;
      width: 60px;
      height: 60px;
      line-height: 60px;
      font-size: 14px;
      text-align: center;
      font-weight: bold;
      color: #333; }
    .header .level-selector .select {
      position: relative;
      width: 500px;
      height: 60px;
      margin-left: 60px;
      border-left: 1px solid  #ccc;
      border-right: 1px solid  #ccc;
      box-sizing: border-box;
      cursor: pointer;
      transition: all .15s ease; }
      .header .level-selector .select::after {
        content: "\f35f";
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-size: 20px;
        color: #555;
        font-family: "Ionicons";
        transition: color .15s ease; }
      .header .level-selector .select .value {
        margin-left: 15px;
        line-height: 60px;
        font-size: 18px; }
    .header .level-selector .dropdown {
      position: absolute;
      top: 80px;
      left: 60px;
      width: 500px;
      visibility: hidden;
      background: #fff;
      border: 1px solid  #ccc;
      box-sizing: border-box;
      z-index: 9999;
      opacity: 0;
      transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1); }
      .header .level-selector .dropdown .item {
        height: 35px;
        padding: 0 15px;
        line-height: 35px;
        cursor: pointer;
        transition: all .15s ease; }
        .header .level-selector .dropdown .item:not(:last-child) {
          border-bottom: 1px solid  #ccc; }
        .header .level-selector .dropdown .item:hover {
          background: #3498db;
          color: #fff; }
    .header .level-selector:not(.open) .select:hover {
      background: #3498db;
      border-color: #3498db;
      color: #fff; }
      .header .level-selector:not(.open) .select:hover::after {
        color: #fff; }
    .header .level-selector.open .select {
      background: #eee; }
    .header .level-selector.open .dropdown {
      visibility: visible;
      top: 60px;
      opacity: 1;
      transition: all 0.6s cubic-bezier(0.215, 0.61, 0.355, 1); }
  .header .generate-code,
  .header .clear-figures {
    float: right;
    width: 70px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    font-size: 35px;
    color: #444;
    cursor: pointer;
    transition: all .15s ease; }
  .header .generate-code:hover {
    background: #f1c40f;
    color: #fff; }
  .header .clear-figures:hover {
    background: #e74c3c;
    color: #fff; }

.figure {
  float: right;
  overflow-y: scroll;
  overflow-x: hidden;
  width: 640px;
  height: 100%; }
  .figure::-webkit-scrollbar {
    width: 5px;
    margin: 10px 0;
    background: #ddd; }
  .figure::-webkit-scrollbar-button {
    display: none; }
  .figure::-webkit-scrollbar-thumb {
    width: 15px;
    background: #aaa; }
  .figure .playground {
    position: relative;
    width: 100%;
    height: 1000px; }
    .figure .playground .arrows {
      width: 100%;
      height: 100%; }
      .figure .playground .arrows svg {
        width: 100%;
        height: 100%; }
        .figure .playground .arrows svg path {
          stroke-width: 4px;
          stroke: #555;
          fill: none; }
    .figure .playground .start {
      position: absolute;
      top: 50px;
      left: 320px;
      width: 180px;
      height: 50px;
      margin: -25px -90px;
      border-radius: 100%;
      background: #e74c3c;
      text-align: center;
      line-height: 50px;
      color: #fff;
      font-size: 16px;
      font-weight: bold; }
      .figure .playground .start::before {
        content: "Start"; }
    .figure .playground .loop {
      position: absolute;
      width: 180px;
      height: 50px;
      margin: -25px -90px;
      background: #d35400;
      text-align: center;
      line-height: 50px;
      color: #fff; }
      .figure .playground .loop::before, .figure .playground .loop::after {
        content: "";
        position: absolute; }
      .figure .playground .loop.begin {
        top: 150px;
        left: 320px; }
        .figure .playground .loop.begin::before {
          top: 0;
          left: 0;
          border-top: 0;
          border-left: 15px solid  #fff;
          border-bottom: 15px solid transparent;
          border-right: 15px solid transparent; }
        .figure .playground .loop.begin::after {
          top: 0;
          right: 0;
          border-top: 0;
          border-left: 15px solid transparent;
          border-bottom: 15px solid transparent;
          border-right: 15px solid  #fff; }
      .figure .playground .loop.end {
        top: 310px;
        left: 320px; }
        .figure .playground .loop.end::before {
          bottom: 0;
          left: 0;
          border-top: 15px solid transparent;
          border-left: 15px solid  #fff;
          border-bottom: 0;
          border-right: 15px solid transparent; }
        .figure .playground .loop.end::after {
          bottom: 0;
          right: 0;
          border-top: 15px solid transparent;
          border-left: 15px solid transparent;
          border-bottom: 0;
          border-right: 15px solid  #fff; }
    .figure .playground .field .choose {
      position: absolute;
      top: 230px;
      left: 320px;
      width: 150px;
      height: 60px;
      margin: -30px -75px;
      background: #fff;
      border: 3px dashed  #aaa;
      box-sizing: border-box;
      line-height: 54px;
      text-align: center;
      font-size: 12px;
      color: #888;
      transition: all .2s ease;
      cursor: pointer; }
      .figure .playground .field .choose::before {
        content: "Choose a element"; }
      .figure .playground .field .choose:hover {
        border-color: #111;
        color: #111; }
      .figure .playground .field .choose.focus {
        border-style: solid;
        border-color: #8e44ad;
        color: #8e44ad; }
    .figure .playground .field .figure {
      position: absolute;
      width: 150px;
      height: 80px;
      margin: -40px -75px;
      background: #f0f0f0; }

.body-right {
  background: #eee; }
  .body-right .parts {
    width: 320px; }
    .body-right .parts .head {
      font-size: 12px;
      line-height: 40px;
      font-weight: bold;
      text-align: center; }
    .body-right .parts .row {
      display: flex;
      justify-content: center; }
    .body-right .parts .if {
      position: relative;
      width: 140px;
      height: 60px;
      margin: 10px;
      text-align: center;
      line-height: 60px;
      font-size: 13px;
      z-index: 1; }
      .body-right .parts .if::before, .body-right .parts .if::after {
        content: "";
        position: absolute;
        top: 0;
        width: 0;
        height: 0;
        border-top: 30px solid transparent;
        border-bottom: 30px solid transparent;
        z-index: -1; }
      .body-right .parts .if::before {
        left: 0;
        border-left: 0;
        border-right: 70px solid #f1c40f; }
      .body-right .parts .if::after {
        right: 0;
        border-left: 70px solid #f1c40f;
        border-right: 0; }
      .body-right .parts .if.mark {
        font-weight: bold; }
    .body-right .parts .condition {
      width: 40px;
      height: 40px;
      margin: 10px;
      border-radius: 100%;
      text-align: center;
      line-height: 40px;
      font-size: 12px; }
      .body-right .parts .condition.black {
        background: #111;
        color: #fff; }
        .body-right .parts .condition.black::before {
          content: '黒'; }
      .body-right .parts .condition.white {
        box-sizing: border-box;
        border: 1px solid  #aaa;
        background: #fff;
        color: #111; }
        .body-right .parts .condition.white::before {
          content: '白'; }
    .body-right .parts .action {
      width: 90px;
      height: 40px;
      margin: 10px;
      background: #3498db;
      line-height: 40px;
      text-align: center;
      font-size: 12px;
      color: #fff; }
    .body-right .parts .if,
    .body-right .parts .condition,
    .body-right .parts .action {
      opacity: 0.7; }
    .body-right .parts .selected {
      font-weight: bold;
      transform: scale(1.1);
      cursor: default; }
    .body-right .parts.show-if .if {
      transition: all .2s ease;
      cursor: pointer;
      opacity: 1; }
      .body-right .parts.show-if .if:hover {
        transform: scale(1.1); }
    .body-right .parts.show-action .action {
      transition: all .2s ease;
      cursor: pointer;
      opacity: 1; }
      .body-right .parts.show-action .action:hover {
        transform: scale(1.1); }
    .body-right .parts.condition .condition {
      transition: all .2s ease;
      cursor: pointer;
      opacity: 1; }
      .body-right .parts.condition .condition:hover {
        transform: scale(1.1); }
